<template>
	<div class="inline-container" :class="appendClass" ref="container">
		<template v-if="type==='text' || type === 'password'">
			<!--  -->
			<div class="m-input-prepend" v-if="$slots.prepend">
				<slot name="prepend"></slot>
			</div>
			
			<!--  -->
			<input
				class="m-input" 
				autoComplete="new-password"
				:type="type === 'password' ? passwordType : type"
				v-bind="$attrs"
				v-model="inputValue"
				:disabled="disabled"
				v-on="myListeners"
				:class="styleClass"
				:maxlength="maxlength"
				@blur="validateData"
				ref="input"
			>
			
			<!--  -->
			<span class="hide-password" @click="showOrHidePassword" v-if="type === 'password'">
				<!--  -->
				<m-icon :icon="passwordTag % 2 === 0 ? 'eye' : 'eye-close'"></m-icon>
			</span>
			
			<!--  -->
			<span class="m-input-clear">
				<m-icon v-if="showClearable" @click.native="clearInputValue" class="btn-close-icon" icon="close"></m-icon>
			</span>
			
			<!--  -->
			<div class="m-input-append" v-if="$slots.append">
				<slot name="append"></slot>
			</div>
		</template>
		
		<!--  -->
		<template v-else>
			<textarea
				class="m-textarea" 
				v-bind="$attrs"
				v-model="inputValue"
				v-on="myListeners"
				@blur="validateData"
			>
			</textarea>
		</template>
	</div>
</template>

<script>
import MIcon from '../icon/index.vue'
export default {
	name: 'm-input',
	inject: {
		Form: {
			default: null
		},
		FormItem: {
			default: null
		}
	},
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		// inputtexttextarea
		type: {
			type: String,
			default: 'text',
			validator: (value) => {
				return ['textarea', 'text', 'password'].includes(value)
			}
		},
		// 
		size: {
			type: String,
			default: '',
			validator: (value) => {
				return ['', 'medium', 'small'].includes(value)
			}
		},
		// 
		clearable: {
			type: Boolean,
			default: false
		},
		// 
		center: {
			type: Boolean,
			default: false
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		maxlength: {
			type: Number,
			default: 100
		}
	},
	components: {
		MIcon
	},
	data() {
		return {
			passwordType: 'password',
			// 
			passwordTag: 0,
			// input
			inputWidth: 0
		} 
	},
	mounted() {
		if(this.FormItem) {
			this.FormItem.$on('reset', () => {
				this.inputValue = ''
			})
		}
		
		// input
		if(this.type === 'password' || this.clearable) {
			// 
			this.$refs.container.style.width = this.$refs.input.offsetWidth + 'px'
			// paddingRight
			this.$refs.input.style.paddingRight = '32px'
		}
		
	},
	methods: {
		clearInputValue() {
			this.inputValue = ''
		},
		// 
		validateData() {
			// 
			// formitem
			if(this.FormItem) {
				this.FormItem.$emit('validate')
			}
		},
		showOrHidePassword() {
			this.passwordType = this.passwordType === 'password' ? 'text' : 'password'
			this.passwordTag++
		}
	},
	computed: {
		inputValue: {
			set(value){
				this.$emit('input', value)
			},
			get() {
				return this.value
			}
		},
		myListeners() {
			return Object.assign(this.$listeners, {
				input: event => this.$emit('input', event.target.value)
			})
		},
		styleClass() {
			return {
				[`m-input-${this.size}`]: this.size,
				[`is-center`]: this.center,
				[`is-disabled`]: this.disabled
			}
		},
		// 
		showClearable() {
			return this.clearable && this.inputValue !== ''
		},
		// 
		appendClass() {
			return {
				'has-prepend': this.$slots.prepend,
				'has-append': this.$slots.append
			}
		}
	}
}
</script>

<style scoped lang="scss">
@import './style.scss';
</style>
