<template>
	<div class="m-form-item">
		<label class="m-form-item-label">
			{{ label }}
		</label>
		<div class="m-form-item-container">
			<slot></slot>
			<p v-if="error" class="m-form-item-container-warning">{{ error }}</p>
		</div>
	</div>
</template>

<script>
import Schema from '../../assets/js/validate.js'
// import Schema from 'async-validator'
export default {
	name: 'm-form-item',
	inject: ['Form'],
	provide() {
		return {
			FormItem: this
		}
	},
	props: {
		prop: {
			type: String
		},
		label: {
			type: String
		}
	},
	data() {
		return {
			error: ''
		}
	},
	mounted() {
		// input
		this.$on('validate', () => {
			this.validate()
		})
		// $children$parentprovideinject
		// 
		if(this.prop) {
			this.Form.$emit('add.Fileds', this)
			this.Form.$emit('reset.fileds', this)
		}
	},
	methods: {
		// 
		validate() {
			// 
			const value = this.Form.modal[this.prop]
			// 
			const rule = this.Form.rules[this.prop]
			// 
			const descriptor = {
				[`${this.prop}`]: rule
			}
			// 
			const validator = new Schema(descriptor)
			// 
			// Promise
			return validator.validate({
				[`${this.prop}`]: value
			}, (error) => {
				if(error) {
					this.error = error[0].message
				} else {
					// 
					this.error = ''
				}
			})
		},
		resetFileds() {
			this.error = ''
			// input
			this.$emit('reset')
		}
	}
}
</script>

<style scoped lang="scss">
@import './style.scss'
</style>
