<template>
	<button @click="handleClick" class="m-button" :class="styleClass">
		<svg v-if="showIconLeft" class="icon" :class="{ 'loading': loading }" aria-hidden="true">
			<use :xlink:href="iconName"></use>
		</svg>
		<slot></slot>
		<svg v-if="showIconRight" class="icon" :class="{ 'loading': loading }" aria-hidden="true">
			<use :xlink:href="iconName"></use>
		</svg>
	</button>
</template>

<script>
export default {
	name: 'm-button',
	props: {
		// 
		type: {
			type: String,
			default: 'default'
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		size: {
			type: String,
			default: 'default'
		},
		// 
		icon: {
			type: String,
			default: ''
		},
		iconLeft: {
			type: String,
			default: ''
		},
		iconRight: {
			type: String,
			default: ''
		},
		// 
		round: {
			type: Boolean,
			default: false
		},
		loading: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		styleClass () {
			return {
				[`m-button--${this.type}`]: true,
				[`is-disabled`]: this.disabled,
				[`m-button-${this.size}`]: true,
				'is-round': this.round,
				'loading': this.loading
			}
		},
		// iconicon-lefticon-right
		iconName () {
			return `#icon-${ this.icon || this.iconLeft || this.iconRight }`
		},
		showIconLeft () {
			return this.iconLeft || this.icon
		},
		showIconRight() {
			return this.iconRight
		}
	},
	methods: {
		handleClick (e) {
			// ,
			if(this.disabled) {
				return false
			}
			this.$emit('click', e)
		}
	},
}
</script>

<style scoped lang="scss">
@import './index.scss';
</style>
